html, body {
  margin: 0;
  padding: 0;
  background-color: black;
  font-family: 'Lobster', cursive;
}

.background {    
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  z-index: 0;
}

.main {
  position: absolute;
  padding: 150px;
  color: white;
  font-size: 72px;
}

@media only screen and (max-width: 600px) {
  .main {
    padding: 80px;
    font-size: 52px;
    text-align: center;
  }
}